/* Parent container for alignment */
.info-container {
    display: flex;
    justify-content: center;
    gap: 1rem;
    color: rgb(0, 0, 0);
    position: relative;
    width: 85%;
    margin: auto;
    box-sizing: border-box;
    overflow: hidden;
}

/* Home gallery container */
.home-gallery {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
    padding-top: 10px;
    margin-left: 0;
    margin-right: 0;
    box-sizing: border-box;
}

/* Welcome container */
.welcome-container {
    background-color: #f0f0f0;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    padding: 20px;
    width: 50%;
    height: 50%;
    margin-top: 20px;
    box-sizing: border-box;

}

.welcome-heading {
    font-size: 1rem;
    margin-bottom: 10px;
    color: #333;
    padding: 0;
    font-weight: bold;
}



/* Moving Text */
.moving-text {
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    /* Prevent text from wrapping */
    box-sizing: border-box;
    animation: moveText 50s linear infinite;
    /* Animation for moving text */
    color: rgb(22, 19, 19);
    font-weight: bold;
    z-index: 1;
    position: relative;
    margin-top: 0.5rem;
    /* Ensure it stays within the container */
}

/* Example animation (moving the text from right to left) */
@keyframes moveText {
    0% {
        transform: translateX(100%);
        /* Start from the right */
    }

    100% {
        transform: translateX(-100%);
        /* Move to the left */
    }
}


/* Media Queries for Mobile */
@media (max-width: 400px) {

    body,
    html {
        height: 100%;
        overflow-y: hidden;
        margin: 0;
        font-size: 14px;
        /* Adjust font size */
    }



    /* Info container with adjusted gap and width */
    .info-container {
        display: flex;
        flex-direction: column;
        /* Stack elements vertically */
        align-items: center;
        width: 100%;
        gap: 10px;
        /* Adjust gap for mobile */
        padding: 10px;
        /* Add padding */
        box-sizing: border-box;
    }

    /* Adjust home gallery for mobile screens */
    .home-gallery {
        width: 80%;
        /* Adjust width */
        padding-top: 15px;
    }


    /* Parent container for alignment */
